import './templates.css';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';
import one from './One/one.png';
import two from './Two/two.png';
// import blank from './Blank/blank.png';


function Templates(){
	return(
		<div className='Templates'>
				<div className='templatesVariety'> 
			
					<div className='template'>
						<img className='TempImg' src={one} />
						<Link style={{ textDecoration: 'none' }} to="/resume-hub/templates/one"><button className='use'>Use this template</button></Link>
					</div>

					<div className='template'>
						<img className='TempImg' src={two} />
						<Link style={{ textDecoration: 'none' }} to="/resume-hub/templates/two"><button className='use'>Use this template</button></Link>
					</div>
					
					
					
				

				</div>
			</div>
		);
}

export default Templates;