import './navbar.css';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';
import menu from './menu.png';
import coffee from './coffee.png';
import resume from './resume.svg';	

const NavBar = ({openNav,closeNav,clickedTempHome,clickedContactHome,clickedFaqHome,clickedrHome}) =>{

	openNav=()=>{
      var width = window.innerWidth;
      if(width <=900 && width > 800)
      {
        document.getElementById("myNav").style.width = "35%";
      }
      else if(width <=800 && width > 700)
      {
        document.getElementById("myNav").style.width = "40%";
      }
      else if(width <=700 && width > 600)
      {
        document.getElementById("myNav").style.width = "45%";
      }
      else if(width <=600 && width >500)
      {
        document.getElementById("myNav").style.width = "50%";
      }
      else if(width <=500 && width> 400)
      {
        document.getElementById("myNav").style.width = "62%";
      }
      else if(width <=400 && width > 200)
      {
        document.getElementById("myNav").style.width = "75%";
      }
      else if(width <=200 && width > 0)
      {
        document.getElementById("myNav").style.width = "100%";
      }
    }
    closeNav=()=>{
      document.getElementById("myNav").style.width = "0%";
    }
    clickedrHome=()=>{
    	document.getElementById('tempHome').style.textDecoration ='none';
    	document.getElementById('contactHome').style.textDecoration ='none';
    	document.getElementById('faqHome').style.textDecoration ='none';
    	document.getElementById('tempHome').style.fontWeight ='normal';
    	document.getElementById('contactHome').style.fontWeight ='normal';
    	document.getElementById('faqHome').style.fontWeight ='normal';
    }
    clickedTempHome=()=>{
    	document.getElementById('tempHome').style.textDecoration ='underline';
    	document.getElementById('contactHome').style.textDecoration ='none';
    	document.getElementById('faqHome').style.textDecoration ='none';
    	document.getElementById('tempHome').style.fontWeight ='bold';
    	document.getElementById('contactHome').style.fontWeight ='normal';
    	document.getElementById('faqHome').style.fontWeight ='normal';
    }
    clickedContactHome=()=>{
    	document.getElementById('tempHome').style.textDecoration ='none';
    	document.getElementById('contactHome').style.textDecoration ='underline';
    	document.getElementById('faqHome').style.textDecoration ='none';
    	document.getElementById('tempHome').style.fontWeight ='normal';
    	document.getElementById('contactHome').style.fontWeight ='bold';
    	document.getElementById('faqHome').style.fontWeight ='normal';
    }
    clickedFaqHome=()=>{
    	document.getElementById('tempHome').style.textDecoration ='none';
    	document.getElementById('contactHome').style.textDecoration ='none';
    	document.getElementById('faqHome').style.textDecoration ='none';
    	document.getElementById('tempHome').style.fontWeight ='normal';
    	document.getElementById('contactHome').style.fontWeight ='normal';
    	document.getElementById('faqHome').style.fontWeight ='normal';
    }
	

	return(
		<div className='NavBar' >
			<div className='logo'>
				<a id='rHome' onClick={clickedrHome}><Link style={{ textDecoration: 'none' }} to="/resume-hub/"><img className='logoPic' src={resume} /></Link></a>

				<a id='rHome' onClick={clickedrHome}><Link style={{ textDecoration: 'none' }} to="/resume-hub/"><h2 className='logoT'>Resume Hub</h2></Link></a>
			</div>
			<div className='menu'>
				<Link style={{ textDecoration: 'none' }} to="/resume-hub/templates">
				<p className='menuItem'>
					
						<a id='tempHome' onClick={clickedTempHome}><b>Templates</b></a>
					
				</p></Link>
				<Link style={{ textDecoration: 'none' }} to="/resume-hub/contact">
				<p className='menuItem'>
					<a id='contactHome' onClick={clickedContactHome}><b>Contact</b></a>
				</p>
				</Link>
				{/*<p className='menuItem'>
					<a id='faqHome' onClick={clickedFaqHome}>FAQs</a>
				</p>*/}
				<Link style={{ textDecoration: 'none' }} to="/resume-hub/support/"><img className='coffee' onClick={clickedFaqHome} src={coffee}/></Link>
				<img className='m' onClick={openNav}  src={menu}/> 
				
				
			</div>


			<div id="myNav" class="overlay">
		        <a href="javascript:void(0)" class="closebtn" onClick={closeNav}>&times;</a>
		        <div class="overlay-content">
		            <a onClick={closeNav}><Link to="/resume-hub/templates">Templates</Link></a>
		            <a onClick={closeNav}><Link to="/resume-hub/contact">Contact</Link></a>
		            {/*<a onClick={closeNav}><Link to="/resume-hub/donate">FAQs</Link></a>*/}
		            
		      	</div>
   		</div>
			</div>
		);
}

export default NavBar;